<template>
  <div style="display: none">
    <router-link id="home-link" to="/"></router-link>
    <router-link id="gallery-link" to="/gallery"></router-link>
  </div>
  <transition name="opacity">
    <button v-if="showback" class="back">
      <router-link to="/">
        <i class="fas fa-arrow-left"></i>
      </router-link>
    </button>
    <button v-else @click="changeTheme" class="theme-switch">
      <transition name="slide-top" mode="out-in">
        <i class="fas fa-sun" v-if="theme === 'dark'"></i>
        <i class="fas fa-moon" v-else></i>
      </transition>
    </button>
  </transition>
  <router-view v-slot="{ Component }">
    <transition name="opacity" mode="out-in">
      <component :is="Component" />
    </transition>
  </router-view>
  <transition name="opacity">
    <p-loading v-if="isLoading" @finished-loading="isLoading = false" />
  </transition>
</template>

<script>
import PLoading from "./components/PLoading.vue";
export default {
  name: "app",
  components: { PLoading },
  watch: {
    $route(newRoute) {
      if (newRoute.path.length < 2) {
        this.showback = false;
      } else {
        this.showback = true;
      }
      if (newRoute.path === "/gallery") {
        document.getElementById("home-link").click();
        setTimeout(document.getElementById("gallery-link").click(), 1000);
      }
    },
  },
  data() {
    return {
      isLoading: true,
      showback: false,
      theme: this.mountedTheme,
    };
  },
  computed: {
    mountedTheme() {
      return localStorage.getItem("theme");
    },
  },
  methods: {
    changeTheme() {
      document.body.classList.toggle("dark");
      if (this.theme === "dark") {
        localStorage.setItem("theme", "light");
        this.theme = "light";
      } else {
        localStorage.setItem("theme", "dark");
        this.theme = "dark";
      }
    },
  },
  mounted() {
    document.body.classList.add(this.mountedTheme);
  },
};
</script>

